<template>
  <div class="question">
    <div v-show='step1' class="fill-title">
      <div class="button-bar">
        <router-link to="/home">
          <a class="cancel"></a>
        </router-link>
        <label v-show='titleLimit1'>&nbsp;{{titleCount}}&nbsp;</label>
        <label class='warning' v-show='titleLimit2'>&nbsp;{{titleExceed}}&nbsp;</label>
        <a class="next-step" v-if='titleNext' @click='goStep2'></a>
        <a class="next-step fake" v-else='titleNext' href="javascript:;"></a>
      </div>
      <div class="input">
        <textarea v-model='title' @input='checkTitle' placeholder="" autofocus></textarea>
      </div>
    </div>
    <div v-show='step2' class="fill-describle">
      <div class="button-bar">
          <span @click="backStep1">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-fanhui"></use>
            </svg>
          </span>
        <a class="next-step" @click='goStep3'></a>
      </div>
      <div class="input">
        <textarea v-model='describe' placeholder="" ></textarea>
      </div>
    </div>
    <div v-show='step3' class="fill-topic">
      <div class="button-bar">
          <span @click="backStep2">
            <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-fanhui"></use>
          </svg>
          </span>
        <div class="title">{{topicNotice}}</div>
        <a v-if='permitSubmit' class="next-step" @click="commit"></a>
        <a class="next-step fake" v-else></a>
      </div>
      <div class="search">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-sou"></use>
        </svg>
        <div class="topic-label">
          <label v-for='item in topicList' @click="deleteTopic(item)">{{item}}</label>
        </div>
        <div>
          <input v-model="topic" type="text" placeholder="" autofocus @input="topicSearch">
        </div>
      </div>
      <div class="topic-preview">
        <div class="topic-list" v-for="topic in topicPreviewList" @click="addTopic(topic.name)">
          <div class="img"><img src="../../img/3.jpg" alt=""></div>
          <div class="content">
            <div class="topic-name">{{topic.name}}</div>
            <div class="description"></div>
          </div>
        </div>
      </div>
      <div class="loading" v-show='loading_gif'>
        <!---->
        <loading v-show='loading'></loading>
        <!---->
        <status v-show='status' :status='axStatus'></status>
      </div>
    </div>
  </div>
</template>
<script>
  import Axios from 'axios'
  import AutoSize from '../../js/autosize.js'
  import Loading from '../../components/loading/loading.vue'
  import Status from '../../components/loading/status.vue'
  export default {
    name: 'question',
    data () {
      return {
        step1: true,
        step2: false,
        step3: false,
        titleLimit1: false,
        titleLimit2: false,
        titleNext: false,
        title: '',
        titleCount: '',
        titleExceed: '',
        describe: '',
        topic: '',
        topicNotice: '', // 
        topicPreviewList: [], // 
        topicList: [], // 
        loading: false, // 
        loading_gif: false, // 
        axStatus: false, // 
        status: false
      }
    },
    mounted: function () {
      AutoSize(document.querySelectorAll('textarea'))
    },
    components: {
      Loading,
      Status
    },
    watch: {
      topicList: function () {
        if (this.topicList.length === 0) {
          this.topicNotice = ''
        } else if (this.topicList.length === 5) {
          this.topicNotice = ''
        } else {
          this.topicNotice = ''
        }
      }
    },
    computed: {
      permitSubmit: function () {
        if (this.title && this.topicList.length > 0 && this.topicList.length <= 5) {
          return true
        }
      }
    },
    methods: {
      goStep2 () {
        this.step1 = false
        this.step2 = true
      },
      goStep3 () {
        this.step2 = false
        this.step3 = true
      },
      backStep1 () {
        this.step2 = false
        this.step1 = true
      },
      backStep2 () {
        this.step3 = false
        this.step2 = true
      },
      commit () {
        this.loading_gif = true
        this.loading = true
        Axios.post('/question/addQuestion', {
          title: this.title,
          describe: this.describe,
          topic: this.topicList
        })
        .then((response) => {
          if (response.data) {
            this.loading = false
            this.axStatus = true
            this.status = true
            setTimeout(() => {            // 1
              this.$router.go(-1)
            }, 1000)
          } else {
            this.loading = false
            this.axStatus = false
            this.status = true
            setTimeout(() => {            // 1
              this.$router.go(-1)
            }, 1000)
          }
        })
          .catch((err) => {
            console.log(err.msg)
            this.loading = false
            this.axStatus = false
            this.status = true
            setTimeout(() => {            // 1
              this.$router.go(-1)
            }, 1500)
          })
      },
      checkTitle () {
      /*
        let reg = /.*[//?]$/   // 
        if (reg.test(this.title)) {
          console.log('')
        } else {
          console.log('')
        }
        */
        let count = this.title.length
        if (count === 0) {
          this.titleNext = false
        }
        if (count > 0 && count <= 40) {        // 
          this.titleNext = true
          this.titleLimit1 = false
          this.titleLimit2 = false
        } else if (count > 40 && count <= 50) {  // 50
          this.titleNext = true
          this.titleLimit1 = true
          this.titleLimit2 = false
          this.titleCount = 50 - count
        } else if (count > 50) {
          this.titleNext = false
          this.titleLimit1 = false
          this.titleLimit2 = true
          this.titleExceed = count - 50
        }
      },
      topicSearch () {
        let count = this.topic.length
        if (count > 4) {
          this.topicNotice = '6'
        } else {
          if (this.topicList.length === 0) {
            this.topicNotice = ''
          }
          if (count !== 0) {
            Axios.post('/question/searchTopic', {
              topic: this.topic
            })
            .then((response) => {
              this.topicPreviewList = []
              response.data.forEach((result) => {
                this.topicPreviewList.push({
                  name: result.name
                })
              })
            })
          }
        }
      },
      addTopic (topicName) {
        if (this.topicList.length > 0 && this.topicList.length <= 5) { // 
          for (let i = 0; i < this.topicList.length; i++) {
            if (this.topicList[i] === topicName) {
              break
            }
            if (i === this.topicList.length - 1) {
              this.topicList.push(topicName)
            }
          }
        } else if (this.topicList.length === 0) {
          this.topicList.push(topicName)
        }
      },
      deleteTopic (topicName) {
        for (let i = 0; i < this.topicList.length; i++) {
          if (this.topicList[i] === topicName) {
            this.topicList.splice(i, 1)
          }
        }
      }
    }
  }

</script>
<style lang="scss" scoped>
  @import "../../scss/config";
  .question {
    .icon {
      margin-top: 10px;
      font-size: 23px;
      color: $icon;
    }
    .button-bar {
      width: 100%;
      height: 45px;
      margin-top: 5px;
      display: flex;
      justify-content: space-between;
      a {
        color: #0283FE;
        width: 50px;
        height: 40px;
        line-height: 40px;
        font-size: 16px;
        font-weight: 700;
      }
      .fake {
        color: rgba(2,131,254,0.3);
      }
      .cancel {
        float: left;
        margin-left: 12px;
      }
      .next-step {
        float: right;
        margin-right: 8px;
      }
      span {
        display: inline-block;
        min-width: 40px;
        text-align: center;
      }
      label{
        line-height: 40px;
        font-size: 14px;
        color: $font;
      }
      .warning {
        color: red;
      }
    }
    .input {
      display: flex;
      width: 96%;
      color: $sm-font;
      border-bottom: 1px solid $border;
      font-size: 16px;
      margin: 0 auto;
      textarea {
        width: 100%;
        border: none;
        font-size: 17px;
        justify-content: center;
    }
    }
    .fill-topic {
      .title {
        line-height: 40px;
        font-size: 15px;
      }
      .next-step {
        letter-spacing: 9px;
        text-align: right;
        margin-right: 3px;
      }
      .search {
        border-top: 1px solid $border;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        .topic-label {
          label {
            background: #E8F0FD;
            padding: 3px 8px 3px 8px;
            font-size: 12px;
            color: $font;
            margin-left: 6px;
          }
        }
        .icon {
        }
        input {
          height: 48px;
          font-size: 14px;
          padding-left: 5px;
          border: none;
        }
      }
      .topic-preview {
        width: 100%;
        height: 60px;

        .topic-list {
          display: flex;
          padding: 7px 0 7px 0;
          border-top: 1px solid $border;
          .img {
            flex: 1;
            text-align: center;
            img {
              height: 50px;
              width: 50px;
              border-radius: 2000px;
            }
          }
          .content {
            flex: 4;
            .topic-name {
              height: 20px;
              font-size: 14px;
              color: $pr-font;
              font-weight: 600;
            }
            .description {
              height: 35px;
              width: 95%;
              font-size: 12px;
              color: $sp-font;
              margin-top: 3px;
              display: -webkit-box;
              display: -moz-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              -moz-box-orient: vertical;
              -moz-line-clamp: 2;
              overflow: hidden;
            }
          }
        }
      }
    }
    .loading {
      position: absolute;
      width: 100%;
      top: 200px;
    }
  }

</style>




